import { style } from '../common/utils/style';

export default {
  name: 'PressIconMusic',
  props: {
    width: {
      type: String,
      default: '40px',
    },
    height: {
      type: String,
      default: '50px',
    },
    number: {
      type: Number,
      default: 3,
    },
    color: {
      type: String,
      default: '#2979ff',
    },
  },
  emits: [],
  data() {
    return {

    };
  },
  computed: {
    barList() {
      return Array.from({ length: this.number });
    },
    rootStyle() {
      return style([
        {
          width: this.width,
          height: this.height,
        },
      ]);
    },
    barStyle() {
      const { width, number, color } = this;
      return style([
        {
          backgroundColor: color,
          width: `calc(${width} / ${+number + 1})`,
        },
      ]);
    },
  },
  methods: {

  },
};
